/* MAIN PAGE */ 
.mainPage .Logo{
  padding: 1px 2em;
  width: 35%;
  margin: 5% auto 3%;
  font-size: 2.5rem;
}

.mainPage .infoBox{
  background-color: rgba(108, 143, 143, 0.589);
  font-family: 'Comic Neue', cursive;
  font-size: 1.5rem;
  color: #F3EAC2;
  padding: 1%;
  border-radius: 20px;
  width: 70%;  
  margin: 3% auto;
  text-shadow: 2px 2px #252525ab;
}

/*** IMG ON MAIN PAGE ***/ 
.mainPage .motionCard {
  width: 20%;
  transform: rotate(15deg);
  position: absolute;
  z-index: -1;
  right: 90%;
  top: 50%;
}

.mainPage .colorChangeCard{
  width: 20%;
  transform: rotate(25deg);
  position: absolute;
  z-index: 1;
  right: 80%;
  top: 62%;
}

.mainPage .bikeCard{
  width: 20%;
  transform: rotate(35deg);
  position: absolute;
  z-index: +1;
  right: 75%;
  top: 74%;
}
/* **TOP** */ 
.mainPage .dropCardTop{
  width: 20%;
  transform: rotate(25deg);
  position: absolute;
  z-index: +1;
  left: 92%;
  bottom: 70%;
}

.mainPage .colorChangeCardTop{
  width: 20%;
  transform: rotate(30deg);
  position: absolute;
  z-index: 1;
  left: 84%;
  bottom: 70%;
}

.mainPage .motionCardTop{
  width: 20%;
  transform: rotate(35deg);
  position: absolute;
  z-index: -1;
  left: 77%;
  bottom: 80%;
}


