.CardContainer{
    background-color:#FBF4D7;
    color: black;
    margin: 1% 3%;
    height:32rem;
    position:relative;
}

.CardContainerTitle {
    transform-origin: 0% 0%;
    transform: rotate(-90deg) ;
    height: 2rem;
    background-color: #FBF4D7;
    font-family: 'Roboto', sans-serif;
    letter-spacing: 0.1ex;
    position: absolute;
    font-size: 2rem;
    left: -2%;
    bottom: 35%;
    padding: 0.5% 1%;
    z-index: 1;
}


/* CSS-styling for the components/page of AnimationPage.jsx - feel free to incorporate this in a common CSS-file */

/* The styling below belongs to the grid-outline of AnimationsPreview.jsx */

.gridAnimationsPage {
    display: grid;
    grid-template-columns: auto auto auto auto;
    width: 100%;
    position: relative;
    padding-top: 1vh;
    transition: linear all 0.1ms;
}

.gridAnimationItem1 {
    grid-area: 2 / 1 / span 1 / span 1;
    margin: 10px;
    display: grid;
    grid-template-columns: auto auto auto; /* remove one "auto" if we decide to go with 4 cards instad of 3 */
}

.gridAnimationItem2 {
    grid-area: 2 / 2 / span 10 / span 10;
    background-color: #FBF4D7;
    border-radius: 30px;
    height: 32vw;
    padding: 5px;
    position: relative;
    margin-right: 3%;
}

.gridAnimationItem3 {
    grid-area: 3 / 1 / span 3 / span 1;
}

/* Styling of the cards */
.card1AnimationsPage, .card2AnimationsPage, .card3AnimationsPage {
    margin-top: 3vw;
    margin: 3px;
}

.triggerAnimationsPage, .thingAnimationsPage, .feedbackAnimationsPage {
    height: 16vw;
}

.triggerAnimationsPage, .thingAnimationsPage, .feedbackAnimationsPage {
    box-shadow: 0px 12px 17px 2px rgba(0, 0, 0, 0.14),
            0px 5px 22px 4px rgba(0, 0, 0, 0.12),
            0px 7px 8px -4px rgba(0, 0, 0, 0.2);
}

.triggerAnimationsPage:hover, .thingAnimationsPage:hover, .feedbackAnimationsPage:hover {
    opacity: 0.8;
}

/* Styling for saving text file, in FormFieldAnimation.jsx */

.textAreaAnimationPage {
    position: relative;
    bottom: 0px;
    margin: 3%;
    display: flex;
    align-items: center;
}

#textBoxAnimationPage {
    height: 6vh;
    position: relative;
    font-family: sans-serif;
    resize: none;
    width: 80%;
    margin: 3px -1px;
    border-radius: 5px;
    background-image: url(data:image/png;base64,); /* icon from: https://uxwing.com/edit-pen-icon/ (free to use and no attributions required) */
    background-position: 10px 10px; /* placement of the pen-icon in the textarea */
    background-size: 10px;
    padding-left: 25px;
    padding-top: 7px;
    background-repeat: no-repeat;
    font-family: 'Comic Neue', sans-serif;
    margin-right: 10px;
    margin-left: 10px;
}

.textAreaAnimationPage textarea { /* border color for textarea */
    border: 1px solid #F08A00;
}

.textAreaAnimationPage #textBoxAnimationPage:focus { /* shadow for border when user is actively using textarea */
    border: 1px solid #F08A00;
    box-shadow: 0 0 0 0.2rem rgba(180, 102, 0, 0.25);
}

.textButtonAnimationPage {
    width: 50%;
} 

/* Styling below prevents animationpage from going into topcomponent, also including elements from ChosenCardsPage.jsx */

@media only screen and (max-width: 800px) {
    .gridAnimationsPage, .chosenCardsGrid {
        margin-top: 5vh;
    }
    
    #textBoxAnimationPage {
        width: 90%;
    }

    .triggerAnimationsPage, .thingAnimationsPage, .feedbackAnimationsPage {
        height: 15vw;
    }

}

@media only screen and (max-width: 700px) {
    .gridAnimationsPage, .chosenCardsGrid {
        margin-top: 6vh;
    }

    #textBoxAnimationPage {
        width: 88%;
    }

    .triggerAnimationsPage, .thingAnimationsPage, .feedbackAnimationsPage {
        height: 14vw;
    }
}

@media only screen and (max-width: 600px) {
    .gridAnimationsPage, .chosenCardsGrid {
        margin-top: 7vh;
    }

    #textBoxAnimationPage {
        width: 86%;
    }

    .triggerAnimationsPage, .thingAnimationsPage, .feedbackAnimationsPage {
        height: 13vw;
    }
}

@media only screen and (max-width: 500px) {
    .gridAnimationsPage, .chosenCardsGrid {
        margin-top: 8vh;
    }

    
    #textBoxAnimationPage {
        width: 84%;
    }

    .triggerAnimationsPage, .thingAnimationsPage, .feedbackAnimationsPage {
        height: 12vw;
    }
}

@media only screen and (max-width: 400px) {
    .gridAnimationsPage, .chosenCardsGrid {
        margin-top: 9vh;
    }

    .triggerAnimationsPage, .thingAnimationsPage, .feedbackAnimationsPage {
        height: 11vw;
    }
}

